/*eslint-disable*/
import axios from 'axios';
import { showAlert } from './alert';

export const userReviewForm = async (review, rating, productId) => {
  try {
    const res = await axios({
      method: 'POST',
      url: 'http://127.0.0.1:3000/api/v1/reviews',
      data: {
        review: review,
        rating: rating,
        product: productId,
      },
    });

    if (res.data.status === 'success') {
      showAlert('success', 'review Successfully sent');
      window.setTimeout(() => {
        location.assign('/');
      }, 1000);
    }
  } catch (err) {
    showAlert('error', err.response.data.message);
  }
};
